/* 
SIDEBAR
Layout and styling for the main sidebar that appears on the homepage and a select other pages.
*/

.main-sidebar {
    .search-form {
        position: relative;
        display: grid;

        .search-field {
            width: 100%;
        }

        button {
            position: absolute;
            right: var(--space-2xs);
            top: 50%;
            height: calc(100% - var(--space-xs));
            transform: translateY(-50%);
        }
    }

    .discord-link,
    .intro {
        display: flex;
        flex-direction: column;
        gap: var(--space-s);

        .intro {
            gap: var(--space-xs);

            img {
                width: fit-content;
            }
        }
    }

    .author-section-small {
        &__author-block {
            display: flex;
            gap: var(--space-xs);
            padding-block: var(--space-xs);

            .author-avatar {
                min-width: 56px;
                transition: all 0.1s ease-in-out;

                &:hover {
                    filter: drop-shadow(0 0 3px rgba(109, 153, 248, 1));
                }
            }

            .author-block-content {
                display: flex;
                flex-direction: column;
                gap: var(--space-3xs);
            }

            .new-author-img {
                display: flex;
                align-items: center;
                justify-content: center;
                min-width: 56px;
                height: 56px;
                background-color: var(--clr-surface-400);

                img {
                    align-self: center;
                }
            }

            p:not(:first-child) {
                color: var(--clr-text-900);
            }

            &:is(:first-child) {
                padding-block-start: 0;
            }

            &:is(:last-child) {
                padding-block-end: 0;
            }

            &:not(:last-child) {
                border-bottom: 1px solid var(--clr-border-darker);
            }
        }
    }

    .sidebar-img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;

        @media (max-width: 621px) {
            min-width: 100%;
            height: auto;
        }
    }
}
